header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* =============== CTA =============== */
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* =============== HEADER SOCIALS =============== */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    font-size: 2.0rem;
}

.header__socials::after{
    content: '';
    width: 4px;
    height: 6rem;
    background: var(--color-primary);
}

/* =============== ME =============== */
.me {
    background: linear-gradient(var(--color-primary),transparent);
    width: 24rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70% ;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.pro {
    border-radius: 39% 61% 23% 77% / 38% 19% 81% 62% ;
}

/* =============== SCROLL DOWN =============== */
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 10.5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 2rem;
}

/* =============== MEDIA QUERIES (MEDIUM DEVICES) =============== */
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

/* =============== MEDIA QUERIES (SMALL DEVICES) =============== */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials, .scroll__down {
        display: none;
    }
}